.avatar-edit {
  background: #000 url(../../images/editAvatarButton.svg) no-repeat 50%;
  border: none;
  border-radius: 100%;
  height: 120px;
  opacity: 0;
  padding: 0;
  position: absolute;
  width: 120px;
  cursor: pointer;
}
.avatar-edit:hover {
  opacity: 0.8;
}

@media screen and (max-width: 767px) {
  .avatar-edit {
    margin-bottom: 127px;
  }
}
